.build-list-item .talent-icon {
    background-size: calc(100% + 2px) !important;
}
.talent-icon {
    background-position: center center !important;
    position: relative;
}
.talent-icon::after {
    align-content: center;
    display: grid;
    height: 100%;
    color: #fff;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    font-size: 150%;
}
.build-list-item .talent-icon::after {
    border-image: url('../../assets/images/borders/border-10.png');
    border-image-slice: 15;
    border-image-width: 15px;
    border-style: solid;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 100px #00000069;
}
.talent-1::after {
    content: '1';
}
.talent-2::after {
    content: '2';
}
.talent-3::after {
    content: '3';
}
.talent-icon:hover::before {

    content: 'Hitting more than 3 enemies in one swing generates temporary health.';
    height: 40px;
    width: 400px;
    display: grid;
    position: absolute;
    background: radial-gradient(#440c0ca8 10%, #0000 60%) #2d1212;
    top: -55px;
    right: -190px;
    border-image: url('../../assets/images/borders/border-10.png');
    border-image-slice: 15;
    border-image-width: 15px;
    border-style: solid;
    padding: 5px;
    white-space: unset;
    text-transform: initial;
    align-content: center;

}

.talent-left::after {
    content: '1';
}
.talent-middle::after {
    content: '2';
}
.talent-right::after {
    content: '3';
}

