
.hero-talents-container {
  grid-area: heroTalents;
  position: relative;
  height: 578px;
}
.hero-talents-container::before, .hero-talents-container::after {
  content: '';
  position: absolute;
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  left: 0;
}

.hero-talents-container::before {
  background: url('../../assets/images/backgrounds/background26.png') bottom;
  box-shadow: inset 1000px 1000px 1000px #55191994;
  filter: contrast(175%) saturate(200%);
}

.hero-talents-container::after {
  border-image: url('../../assets/images/borders/border-01.png');
  border-image-slice: 15;
  border-image-width: 15px;
  border-style: solid;
  background: radial-gradient(#000000c4,#00000073);
}

.hero-talents-grid {
  display: grid;
  grid-template-rows: repeat(6, minmax(0px,88px));
  grid-template-columns: 85px 1fr 1fr 1fr;
  grid-template-areas: "heroTalentsHeader heroTalentsHeader heroTalentsHeader heroTalentsHeader"
                       "heroTalentLevel5 heroTalent1 heroTalent2 heroTalent3"
                       "heroTalentLevel10 heroTalent4 heroTalent5 heroTalent6"
                       "heroTalentLevel15 heroTalent7 heroTalent8 heroTalent9"
                       "heroTalentLevel20 heroTalent10 heroTalent11 heroTalent12"
                       "heroTalentLevel25 heroTalent13 heroTalent14 heroTalent15"
                       "heroTalentLevel30 heroTalent16 heroTalent17 heroTalent18";
  grid-row-gap: 5px;
  position: absolute;
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  padding: 10px;
  color: #c15b24;
}
.hero-talents-grid * {
  z-index: 1;
}
.hero-talents-header {
  grid-area: heroTalentsHeader;
  font-size: 200%;
  text-transform: uppercase;
  color: #c15b24;
  align-self: center;
  display: none;
}

.talent-lock-icon {
  background: url('../../assets/images/icons/talent-lock-icon.png') no-repeat center;
  position: relative;
}
.talent-lock-icon::after {
  position: absolute;
  height: 100%;
  width: 100%;
  display: grid;
  align-content: center;
  z-index: 99999999999;
  font-size: 150%;
  color: #c15b24;
  margin-top: 6px;
}
.talent-lock-icon.level-5::after {
  content: '5';
}
.talent-lock-icon.level-10::after {
  content: '10';
}
.talent-lock-icon.level-15::after {
