.hero-talent {    
  background-repeat: no-repeat;
  position: relative;
  background-color: #00000085;
  box-shadow: inset 0px 10px 10px -10px white, 0px -1px 0 0 #564640;
  background-position: left center;
  background: none;
  padding: 0;
}
.hero-talent::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: calc(100% - 6px);
  width: calc(100% - 6px);
  border-image: url('../../assets/images/borders/border-01.png');
  border-image-slice: 15;
  border-image-width: 15px;
  border-style: solid;
  z-index: 1;
}
.hero-talent.selected::after {
  border-image: url('../../assets/images/borders/border-16.png');
  border-image-slice: 15;
  border-image-width: 15px;
  border-style: solid;
}
.hero-talent:nth-of-type(3n+1) {    
  box-shadow: inset 0px 10px 10px -10px white, -1px -1px 0 0 #564640;
}
.hero-talent .talent-name {
  font-size: 150%;
  align-content: center;
  height: 100%;
  display: grid;
  text-align: left;
  font-family: caslon-antique-bold;
  color: #c15b24;
  padding-left: 8px;
  padding-right: 10px;
}
.hero-talent.selected .talent-name, .hero-talent:hover .talent-name {
  background: linear-gradient(-16deg, #c626264a 30%, #0000);
  border-radius: 3px;
}
.talent-description {
  position: absolute;
  height: auto;
  left: 0;
  bottom: calc(100% + 2px);
  width: calc(100% - 20px);
  font-size: 150%;
  display: none;
  background: black;
  border: 1px solid red;
  color: #c15b24;
  font-family: caslon-antique-bold;
  padding: 10px;
  text-align: left;
}
.talent-description {
  display: none;
}
.hero-talent:hover .talent-description {
  display: grid !important;
}
.hero-talent:hover .talent-description:hover {
  display: none !important;
}
.hero-talents-grid .talent-icon {
  grid-area: talentIcon;
}
.talent-icon {
  background-repeat: no-repeat;
}
.hero-talent .talent-icon {
  border-right: 4px solid #0c0a07;
  background-position-x: calc(50% + 2px) !important;
  box-shadow: inset 0px 10px 10px -10px white, 0px -1px 0 0 #564640;
}
.talent-name {
  grid-area: talentName;
}
.hero-talent:hover {
  cursor: pointer;
}
.talent-button-wrapper {
  display: grid;
  grid-template-areas: "talentIcon talentName";
  background: none;
  grid-template-columns: 2fr 5fr;
  height: 100%;
}