
.weapon-background, .weapon-icon {
    background: url('../../assets/images/backgrounds/icon-background-2.png');
    background-size: calc(100% + 2px);
    position: relative;
    box-shadow: inset 0 5px 1px white;
    cursor: pointer;
  }
  .weapon-container .weapon-icon {
    height: 56px;
    width: 56px;
  }
  .weapon-icon::after {
    content: '';
    width: 100%;
    height: 100%;
    content: ' ';
    position: absolute;
    left: 0;
    top: 0;
  }
  .build-list-item .weapon-icon, .build-list-item .talent-icon, .build-list-item .trait-icon {
      box-shadow: inset 0 2px 1px white;
  }
  .build-list-item .weapon-icon::after {
    background-size: contain !important;
    border-image: url('../../assets/images/borders/border-10.png');
    border-image-slice: 15;
    border-image-width: 15px;
    border-style: solid;
  }
.weapon-icon::after {
    background-size: contain !important;
    border-image: url('../../assets/images/borders/border-04.png');
    border-image-slice: 15;
    border-image-width: 15px;
    border-style: solid;
  }
  .weapon-icon.selected::after {
    border-image: url('../../assets/images/borders/border-30.png');
    border-image-slice: 15;
    border-image-width: 15px;
    border-style: solid;
  }
  .weapon-background::after {
    background: url('../../assets/images/weapons/kruber-halberd.png');
    content:' ';
    width: 100%;
    height: 100%;
    content: ' ';
    position: absolute;
    left: 0;
    top: 0;
    background-size: contain;
  }  
